.container {
    display: flex;
    flex-direction: column;
}


.containerMain {
    display: grid;
    flex-direction: row;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    flex: 1;
    gap: 2rem;
    padding: 2rem;
    margin-left: 250px;
    z-index: 1;
   
}
.card-poke {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid rgb(116, 113, 113);
    color: white;
    padding: 1rem;
    gap: 1rem;
    justify-content: center;
}

.link-name {
    text-decoration: none;
    border: 1px solid rgb(29, 28, 28);
    background-color: rgb(96, 101, 105);
    color: white;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.containerDigimon {
    display: flex;
    text-decoration: none;
    align-items: center;
}

.containerButton {
    display: flex;
    flex-direction: row;
    padding: 1rem;
    align-content: center;
    justify-content: center;
    gap: 10px;
}

.buttonStyle {
    height: 70px;
    width: 250px;
    border-radius: 2vh;
    background-color: rgb(51, 53, 52);
    color:white;
    cursor: pointer;    
}

.buttonStyle:hover{
    background-color: rgb(107, 106, 106);
}


.color-style {
    color: black
}

.card-poke img {
    width: 100%;
    border: 1px solid red;
}